@import "https://fonts.googleapis.com/css2?family=Domine:wght@400..700&display=swap";
:root {
  --accent: #00e6bf;
  --bg1: #764b36;
  --bg1-text: #fcfaf8;
  --bg2: #9b4922;
  --bg2-text: #fcfaf8;
  --bg3: #d1774c;
  --bg3-text: #2c1f17;
  --bg4: #d8baac;
  --bg4-text: #2c1f17;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Domine, serif;
}

html {
  background-color: var(--bg4);
  color: var(--bg4-text);
  font-size: 62.5%;
  line-height: 1.6;
}

p, button, input {
  font-size: 1.6rem;
}

h1 {
  font-size: 3.2rem;
}

header {
  background-color: var(--bg3);
  height: 6.4rem;
  color: var(--bg3-text);
  justify-content: space-between;
  align-items: center;
  padding: .8rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  && button {
    background-color: #0000;
    border: .4rem solid #0000;
    border-bottom-color: #2c1f1700;
    padding: .4rem;
    font-weight: bold;

    &&:hover {
      border-bottom: .4rem solid #2c1f1780;
    }

    &&:focus-visible {
      border: .4rem solid #2c1f1780;
      outline: .2rem solid #fff;
    }

    &&:active {
      border-bottom: .4rem solid var(--bg4-text);
    }
  }
}

main {
  flex-direction: column;
  align-items: center;
  max-width: 1100px;
  margin: 6.4rem auto auto;
  padding: .8rem;
  display: flex;
}
